.modal__close-item {
  position: relative;
  top: -5px;
  left: 480px;
  background-image: url("../images/close-button.svg");
  background-repeat: no-repeat;
  border: none;
  height: 20px;
  width: 20px;
}

.modal__close-item:hover {
  cursor: pointer;
}

.modal__content-item {
  background-color: white;
  max-width: 496px;
  border-radius: 20px;
  padding: 20px 30px 30px 30px;
}
